.carousel{
    position: relative;
    width: 260px;
    height: 205px;
}
.slide{
    border-radius: 0.5rem;
    box-shadow: 0px 0px 7px #6666665d;
    width: 100%;
    height: 100%;
}
.slide-hidden{
    display: none;
}
.carousel>div{
  width: 90%;
  justify-content: space-around;
  position: absolute;
  bottom: 20px;
}
.arrow{
    width: 15px;
    height: 15px ;
    color: white;
}
.arrow:hover{
    cursor: pointer;
}
.indicators{
    display: flex;
    gap: 8px;
}
.indicator{
    background-color: white;
    height: 0.5rem;
    width: 0.5rem;
    border-radius:100%;
    box-shadow: 0px 0px 5px #555;
    cursor: pointer;
    transition: all 0.3s ease;
}
.indicator-inactive{
    background-color: rgba(128, 128, 128, 0.700);
}
.indicator-active{
  width: 0.9rem;
  height: 0.5em;
  border-radius: 2.5px;
}
